.board {
    display: grid;
    grid-template-columns: repeat(8, minmax(30px, 1fr));
    grid-template-rows: repeat(8, minmax(30px, 1fr));
    justify-content: center;
    aspect-ratio: 1;
    width: min(80vw, 445px);
    margin: 0 auto;
    background-color: #222;
    border-radius: 15px;
    padding: min(4vw, 25px);
    gap: min(1vw, 5px);
    overflow: visible;
}

.cell {
    margin: min(1vw, 5px) 0px;
    width: 100%;
    height: 100%;
    aspect-ratio: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: var(--text);
    background-color: #444;
    position: relative;
    cursor: default;
    transition: background-color 0.2s ease;
}
  
.cell.no, .cell.player, .cell.ai {
    background-color: #333;
}

.clickable {
    cursor: pointer;
}

.clickable.ai {
    cursor: default;
}

.moon-contain {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: 100%;
}
  
.lock {
    z-index: 2;
}
  
.mine {
    z-index: 3;
}
  
.mine-count {
    z-index: 3;
    color: #ddd;
}
  
.cellmoon {
    z-index: 1;
}
  
.cellmoon, .lock, .mine {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: contain;
}
  
.hide {
    visibility: hidden;
}
  
.cellmoon.player {
    filter: grayscale(100%) sepia(100%) hue-rotate(270deg) contrast(100%);
}
  
.cellmoon.ai {
    filter: grayscale(100%) sepia(100%) hue-rotate(330deg) contrast(100%);
}
  
.cellmoon.player.show {
    filter: grayscale(100%) sepia(100%) hue-rotate(270deg) contrast(80%);
}
  
.cellmoon.ai.show {
    filter: grayscale(100%) sepia(100%) hue-rotate(330deg) contrast(80%);
}

.cell.selected-piece {
    z-index: 10;
    box-shadow: 0 0 15px 5px var(--primary);
    transition: all 0.3s ease;
    overflow: visible;
}

.row {
    overflow: visible;
}

.clickable .cell.player,
.clickable .cell.empty {
    cursor: pointer;
}

.cell.empty:not(.no) {
    cursor: pointer;
}