.card {
    display: inline-block;
    width: min(20vw, 100px);
    height: min(20vw, 100px);
    margin: 0 min(2vw, 10px);
    border-radius: 0px;
    overflow: visible;
    transition: box-shadow 0.3s ease, border-radius 0.3s ease;
  }
  
  .hand {
    margin-top: min(4vw, 20px);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: min(2vw, 10px);
  }

  .card-contain {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: 100%;
    cursor: pointer;
  }

  .card-contain img {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .card-contain img:first-child {
    z-index: 2;
  }

  .card-contain img:last-child {
    z-index: 3;
  }

  .card-contain .background {
    z-index: 2;
  }

  .card-contain .foreground {
    border-radius: 1000px;
    z-index: 2;
    width: 90%;
    height: 90%;
  }

  .card-contain .glower {
    border: 0px solid #7b9cd9;
    box-shadow: 0 0 50px 40px #7b9cd9;
    width: 0%;
    height: 0%;
  }
